<template>
  <div>

    <div class="tap_tap fix_top" v-if='headerFixed '>
      <div :class="{active_tap:1==$store.state.indexTap}" @click='activeTap(1)'>照片直播</div>
      <div :class="{active_tap:2==$store.state.indexTap}" @click='activeTap(2)'>时间轴</div>
      <div :class="{active_tap:3==$store.state.indexTap}" @click='activeTap(3)'>热门</div>
    </div>

    <div class="tap_tap" ref='TapTap'>
      <div :class="{active_tap:1==$store.state.indexTap}" @click='activeTap(1)'>照片直播</div>
      <div :class="{active_tap:2==$store.state.indexTap}" @click='activeTap(2)'>时间轴</div>
      <div :class="{active_tap:3==$store.state.indexTap}" @click='activeTap(3)'>热门</div>
    </div>

  </div>


</template>

<script>
  export default {
    data() {
      return {
        list: [],
        headerFixed : false,
        offsetHeight:'',
        offsetHeight:''
      }
    },
    mounted() {
      // 监听dom渲染完成
      this.$nextTick(function() {
        // 这里fixedHeaderRoot是吸顶元素的ID
        let header = this.$refs.TapTap
        // 这里要得到top的距离和元素自身的高度
        this.offsetTop = header.offsetTop;
        this.offsetHeight = header.offsetHeight;
        console.log("offsetTop:" + this.offsetTop + "," + this.offsetHeight);
        // this.$store.state.fixheight +=this.offsetHeight/2
      });

    window.addEventListener('scroll', this.handleScroll)
    },
    destroyed() {
      window.removeEventListener('scroll', this.handleScroll);
    },
    methods: {
      activeTap: function(num) {
        this.$store.state.indexTap = num
      },
      handleScroll() {
        // 得到页面滚动的距离
        let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
        // 判断页面滚动的距离是否大于吸顶元素的位置
        this.headerFixed = scrollTop >  this.$store.state.fixheight;
      },

    }

  }
</script>

<!-- (this.offsetTop - this.offsetHeight * 2) -->

<style scoped>
  .tap_tap {
    overflow: hidden;
    box-sizing: border-box;
    padding: 10px 10px;
    color: rgba(0, 0, 0, .5);
    width: 100%;
    background: white;
  }

  .tap_tap div {
    float: left;
    width: 20%;
    text-align: left;
    height: 30px;
    line-height: 30px;
    font-size: 16px;
    position: relative;
    font-style: italic;
    margin-right: 15px;
  }

  .tap_tap div:nth-child(1) {
    width: 20%;
  }

  .tap_tap div:nth-child(2) {
    width: 15%;
  }

  .tap_tap div:nth-child(3) {
    width: 18%;
  }

  .active_tap {
    color: rgba(0, 0, 0, 1);
    font-style: normal;
  }

  .active_tap:after {
    content: '';
    background: red;
    width: 90%;
    position: absolute;
    bottom: 0;
    height: 1px;
    left: 0;
  }

  .fix_top {
    position: fixed;
    top: 0;
    z-index: 99;
  }
</style>



