<template>
  <div>
    <div class="imgcontent">
      <div v-for="(item,index) in imgData" class="imgarea">
          <img :src="item.url" class="animated fadeIn" preview='3' :preview-text="item.txt" :style="{  width: index==0?'100%':index==1?'66.65%':index==2?'33.35%':index==3?'33.35%':'33.33%' }">
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    props: ['imgData'],
    name: 'hello',
    data() {
      return {
        imgWidth: ''
      }
    },
    mounted() {
      this.$nextTick(function(){
      });
    },
    methods: {
      handleClose() {
        console.log('close event')
      }
    }
  }
</script>
<style scoped>
  .imgcontent {
    width: 100%;
    overflow: hidden;
  }

  .imgcontent img {
    float: left;
    padding: .5px;
    box-sizing: border-box;
  }

  .imgcontent .imgarea {
    position: relative;
  }
</style>



