<template>
  <div class="overFlow">
    <el-dialog :visible.sync="dialogVisible" width="80%" top="11vh">
      <img src="../assets/bill.jpg" width="100%" alt />
    </el-dialog>
    <!-- :up="mescrollUp" :down='mescrollDown' -->
    <div>
      <img ref="topImg" @load="onloadimg" src="../assets/top.jpg" alt width="100%" />

      <div ref="toptitle">
        <h3 class="title">礼赞共和国&nbsp;智慧新生活</h3>
      </div>

      <div class="inline-text" ref="toptext">
        <div style="text-align: left;">
          <span style="color: red;">{{usernum}}</span>
          <span>&nbsp;次照片浏览</span>
        </div>
        <div style="text-align: right;" @click="message">
          <span>活动详情</span>
        </div>
      </div>
      <!-- 切换组件 -->
      <Taptap></Taptap>
      <!-- 图片地图 -->
      <Imggroup1 v-if="$store.state.indexTap==1" @setlikes="setlikes" :img-data="imgdata"></Imggroup1>
      <Imggroup2 v-if="$store.state.indexTap==2" :img-data="imgdata"></Imggroup2>
      <Imggroup3 v-if="$store.state.indexTap==3" :img-data="imgdata"></Imggroup3>
    </div>
  </div>
</template>

<script>
import Taptap from "./taptap.vue";
import Imggroup1 from "./imggroup_1.vue";
import Imggroup2 from "./imggroup_2.vue";
import Imggroup3 from "./imggroup_3.vue";
import MescrollVue from "mescroll.js/mescroll.vue";
const axios = require("axios");
import qs from "qs";
//  mescrollUp: {
//     callback: this.cb,
//     htmlLoading: '<p class="upwarp-progress mescroll-rotate"></p><p class="upwarp-tip">加载11中..</p>',
//     htmlNodata: '<p class="upwarp-nodata">-- 没有数据了 --</p>',
//     onScroll: this.scroll
//   },
//   mescrollDown: {
//     use: false,
//   },
export default {
  data() {
    return {
      usernum:0,
      list: [],
      imgdata: [
        {
          url:
            "1fc39559d&imgtype=0&src=http%3A%2F%2Fn.sinaimg.cn%2Fsinacn00%2F100%2Fw1500h1000%2F20180824%2Fe50e-hicsiav9142029.jpg",
          txt: "礼赞共和国  智慧新生活    礼赞共和国  智慧新生活",
          datetime: "2019-08-12 24:24:24",
          likes: "0",
          id: 0
        }
      ],

      dialogVisible: false
    };
  },
  created() {
    this.reload();
    this.onlinenum();
    this.reloadNum();
  },
  mounted() {
    // 监听dom渲染完成
    this.$nextTick(function() {
      // 这里fixedHeaderRoot是吸顶元素的ID
      this.$store.state.fixheight += this.$refs.toptitle.offsetHeight;
      console.log(this.$store.state.fixheight);

      this.$store.state.fixheight += this.$refs.toptext.offsetHeight;
      console.log(this.$store.state.fixheight);
    });
  },
  components: {
    Taptap,
    Imggroup3,
    Imggroup2,
    Imggroup1,
    MescrollVue
  },
  methods: {
    cb(page, mescroll) {},
    scroll(mescroll, y, isUp) {
      console.log(mescroll);
      //           onScroll: this.scroll,
    },
    onloadimg() {
      this.$store.state.fixheight += this.$refs.topImg.offsetHeight;
      console.log(this.$store.state.fixheight);
    },
    message() {
      this.dialogVisible = true;
    },
    reload() {
      axios
        .get("http://localhost:3001/api/getAll")
        .then(response => {
          console.log(response);
          // handle success
          this.imgdata = response.data.msg;
        })
        .catch(function(error) {
          // handle error
          console.log(error);
        })
        .finally(function() {
          // always executed
        });
    },
    setlikes(data){
      this.imgdata=data
    },
    onlinenum(){//增加访问次数
         axios
        .get("http://localhost:3001/api/addusernum")
        .then(response => {
        })
        .catch(function(error) {
          // handle error
          console.log(error);
        })
    },
    reloadNum(){//查询访问次数
         axios
        .get("http://localhost:3001/api/getusernum")
        .then(response => {
          console.log(response);
          this.usernum = response.data.msg[0].onlinenum;
        })
        .catch(function(error) {
          // handle error
          console.log(error);
        })
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.title {
  text-align: left;
  font-size: 17px;
  /* font-weight: 900; */
  padding: 14px;
  letter-spacing: 1px;
}

.inline-text {
  width: 100%;
  padding: 5px 15px 5px 10px;
  box-sizing: border-box;
  position: relative;
  overflow: hidden;
}

.inline-text div:nth-child(1) {
  float: left;
}

.inline-text div:nth-child(2) {
  float: right;
}
.overFlow {
  overflow: hidden;
}

/* .tap_tap div:nth-child(1){

}
.tap_tap div:nth-child(1):after{
    content: '';
    background: red;
    width: 90%;
    position: absolute;
    bottom: 0;
    height: 1px;
    left: 0;
} */
</style>



