<template>
  <div style="position: relative;">
    <!-- <vue-preview  :slides="slide1" @close="handleClose"></vue-preview> -->
    <div
      v-if="imgData&&imgData.length&&imgData.length>0"
      class="imgtap"
      v-for="(item,index) in imgData"
    >
      <div style="position: relative;">
        <el-button round class="rightbtn" @click.stop.prevent="islikes(item.id)">
          <div style="width: 60px;">
            <font-awesome-icon style="color: red;" icon="heart" />
            &nbsp;{{item.likes}}&nbsp;&nbsp;&nbsp;
          </div>
        </el-button>
        <img
          alt
          width="100%"
          :src="item.url"
          class="animated fadeIn"
          preview="1"
          :preview-text="item.txt"
        />
      </div>
      <p>{{item.txt}}</p>
      <!-- 点赞 &nbsp;&nbsp;&nbsp;&nbsp; -->
      <div></div>
    </div>
  </div>
</template>

<script>
import "font-awesome/css/font-awesome.min.css";
const axios = require("axios");
import qs from "qs";

export default {
  props: ["imgData"],
  name: "hello",
  data() {
    return {};
  },
  mounted() {
    this.$previewRefresh();
  },
  methods: {
    handleClose() {
      console.log("close event");
    },
    islikes(id) {
      let data = { id: id };
      axios
        .post(`http://localhost:3001/api/addlike`, qs.stringify(data), {
          headers: {
            "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8"
          }
        })
        .then(res => {
          axios.get("http://localhost:3001/api/getAll").then(response => {
            console.log(response);
            this.$emit("setlikes", response.data.msg);
          });
        });
    },
    reload() {}
  }
};
</script>

<style scoped>
.imgtap p {
  padding: 10px;
  font-size: 13px;
  text-align: left;
  line-height: 20px;
  position: relative;
}
.rightbtn {
  position: absolute;
  z-index: 9;
  right: -20px;
}
.el-button {
  background: rgba(0, 0, 0, 0.5);
  color: #fff;
  cursor: pointer;
  border: none;
  padding: 8px 20px;
}
</style>



